.header{display:flex;flex-direction:row;justify-content:space-between;position:sticky;top:0;z-index:1000;background-color:#fff;padding:20px 7%;border-bottom:1px solid #d9d9d9}.header .info{display:flex;flex-direction:column;gap:12px}.header .info .logo{cursor:pointer}.header .info .logo__img{width:auto;height:40px;max-width:100%;display:block;z-index:1}@media(min-width: 768px){.header .info .logo__img{width:auto}}.contacts{display:flex;flex-wrap:wrap;flex-direction:row;gap:12px}.contacts__item{display:flex;flex-direction:row;gap:4px}.contacts__text a{color:#646464;text-decoration:underline}.contacts__text a:hover{opacity:.7}.burger{position:relative;width:28px;height:20px;display:flex;flex-direction:column;justify-content:space-between;background:none;border:none;cursor:pointer;padding:0;z-index:10}.burger__line{position:absolute;width:100%;height:3px;background-color:#4ca89d;left:0;transition:transform .3s ease,opacity .3s ease}.burger__line:nth-child(1){top:0}.burger__line:nth-child(2){top:8px}.burger__line:nth-child(3){top:16px}.burger.active .burger__line{background-color:#fff}.burger.active .burger__line:nth-child(1){transform:rotate(45deg);top:8px}.burger.active .burger__line:nth-child(2){opacity:0}.burger.active .burger__line:nth-child(3){transform:rotate(-45deg);top:8px}@media(min-width: 768px){.burger{display:none}}